import { Box, Checkbox, FormControlLabel, FormGroup } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import { IBackendField } from "../../../../estreeh-common-library/build/interfaces/IBackendField";
import BackendFieldSet from "../forms/BackendFieldSet";
import SubmitButton from "../forms/SubmitButton";
import PageTitle from "../common/PageTitle";

interface Props extends RouteComponentProps {}

export default function AddWorkerPage(props: Props): ReactElement {
  const mainFields: IBackendField[] = [
    {
      name: "referenceId",
    },
    {
      name: "name",
    },

    {
      name: "jobTitle",
      variant:"select",
    },

    {
      name: "salary",
      type: "number",
    },
    {
      name: "processingFees",
      variant:"select"
    },
    {
      name: "contractPeriod",
    },
    {
      name: "phone",
    },
    // {
    //   name:"hide",
    //   label:"Hide this worker",
    //   variant:"checkbox"
    // }
    {
      name: "localTransfer",
      variant: "checkbox",
    },
  ];

  const localTransferFields: IBackendField[] = [

    {
      name: "localTransferReason",
      variant: "textarea",
    },
  ];

  const personalFields: IBackendField[] = [
    {
      name: "nationality",
      variant:"select"
    },

    {
      name: "dateOfBirth",
      type: "date",
    },
    {
      name: "placeOfBirth",
    },
    {
      name: "martialStatus",
      variant: "select",
    },
    {
      name: "weight",
    },
    {
      name: "complexion",
    },
    {
      name: "religion",
    },
    {
      name: "age",
      type: "number",
    },
    {
      name: "livingTown",
    },
    {
      name: "numberOfChildren",
      type: "number",
    },
    {
      name: "height",
    },
    {
      name: "educationalLevel",
    },
  ];

  const languageFields: IBackendField[] = [
    {
      name: "english",
      variant: "select",
    },
    {
      name: "arabic",
      variant: "select",
    },
  ];

  const remarksFields: IBackendField[] = [
    {
      name: "remarks",
      variant: "textarea",
    },
  ];

  const fileFields: IBackendField[] = [
    {
      name: "smallPicture",
      variant: "file",
      avatarWidth: 80,
    },
    {
      name: "bigPicture",
      variant: "file",
      avatarWidth: 200,
    },
  ];

  const agencyOnlyFields: IBackendField[] = [
    {
      name: "agencyRemarks",
      variant: "textarea",
    },
  ];

  const skills = [
    "Baby Sitting",
    "Cooking",
    "Nursing",
    "Elder Caring",
    "Washing",
    "Ironing",
  ];

  return (
    <Box>
      <PageTitle title="Add a New Worker" />
      <BackendFieldSet fields={mainFields} header="Main Information" />
      <BackendFieldSet fields={localTransferFields}  md={7} />
      <BackendFieldSet fields={personalFields} header="Personal Details" />
      <BackendFieldSet fields={languageFields} header="Language Skills" />
      <BackendFieldSet header="Work Experience" />
      <BackendFieldSet header="Skills" xs={12}>
        <FormGroup row>
          {skills.map((skill) => (
            <FormControlLabel
              control={<Checkbox name={"skill_" + skill} color="primary" />}
              label={skill}
            />
          ))}
        </FormGroup>
      </BackendFieldSet>

      <BackendFieldSet fields={remarksFields} md={7} />

      <BackendFieldSet fields={fileFields} header="Picture Uploads" />
      <BackendFieldSet
        fields={agencyOnlyFields}
        header="Agency Only Information (Hidden)"
        md={7}
      />
      <SubmitButton title="Save" navigate={props.navigate} />
    </Box>
  );
}
