import { Avatar, Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns } from "@material-ui/data-grid";
import { Edit } from "@material-ui/icons";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import IWorkerTwoYear from "../../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import INationality from "../../../../estreeh-common-library/src/interfaces/INationality";
import WorkerTwoYearFactory from "../../factories/WorkerTwoYearFactory";
import PageTitle from "../common/PageTitle";

interface Props extends RouteComponentProps {}

export default function MyWorkersPage(props: Props): ReactElement {
  const maxPPicNumber = 42;


  const columns: GridColumns = [
    // {
    //   field: "id",
    //   sortable: true,
    // },
    {
      field: "referenceId",
      headerName: "REF",
      sortable: true,
      width: 200,
    },
    {
      field: "avatar",
      headerName: "Pic",
      disableColumnMenu: true,
      renderCell: (params) => {
        const workerPicId = ((params.row.id - 1) % maxPPicNumber) + 1;
        return (
          <Avatar
            src={
              "http://demo.estreeh.com/img/Philippines/" +
              workerPicId +
              "_Small.png"
            }
          />
        );
      },
    },

    {
      field: "nationality",
      headerName:"Nationality",
      width:200,
      valueGetter: (params) => {
        const n:INationality = params.row.nationality;
        return n.title;
      }
    },
    {
      field: "name",
      headerName: "Worker Name",
      sortable: true,
      width: 250,
    },
    {
      field: "jobTitle",
      headerName: "Job Title",
      sortable: true,
      description: "Job title of the worker",
      width: 200,
    },
    // {
    //   field: "age",
    //   headerName: "Age",
    //   sortable: false,
    // },
    // {
    //   field: "maritalStatus",
    //   headerName: "Marital Status",
    //   sortable: true,
    //   width: 200,
    // },
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Worker CV",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                if (props.navigate) props.navigate("add");
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },
  ];

  const workers: IWorkerTwoYear[] = WorkerTwoYearFactory.buildList(14);
  console.table(workers);

  return (
    <Box>
      <PageTitle title="My Workers" />
      <Box height={400}>
        <DataGrid
          rows={workers}
          columns={columns}
          pageSize={5}
          checkboxSelection
        />
      </Box>
    </Box>
  );
}
