import { Box, Button, Divider } from "@material-ui/core";
import { NavigateFn } from "@reach/router";
import React, { ReactElement } from "react";

interface Props {
  title: string;
  navigate?: NavigateFn;
  navigateUrl?: string;
}

export default function SubmitButton(props: Props): ReactElement {
  const navigateUrl = props.navigateUrl !== undefined ? props.navigateUrl : "../success";
  return (
    <Box>
      <Box my={3}>
        <Divider />
      </Box>
      <Box
        my={3}
        textAlign="center"
        maxWidth={200}
        marginLeft="auto"
        marginRight="auto"
      >
        <Button
          fullWidth={true}
          variant="contained"
          color="primary"
          onClick={() => {
            if (props.navigate) {
              props.navigate(navigateUrl);
            }
          }}
        >
          {props.title}
        </Button>
      </Box>
    </Box>
  );
}
