import * as Factory from "factory.ts";
import Faker from "faker";
import ILanguageSkill from "../../../estreeh-common-library/build/interfaces/ILanguageSkill";

const LanguageSkillFactory = Factory.Sync.makeFactory<ILanguageSkill>({
  language: Factory.each(() =>
    Faker.random.arrayElement(["English", "Arabic"])
  ),
  level: Factory.each(() => Faker.random.arrayElement([1, 2, 3])),
  levelText: Factory.each(() =>
    Faker.random.arrayElement(["Poor", "Fair", "Fluent"])
  ),
});

export default LanguageSkillFactory;
