import { Box, Button, Grid, Slide, Snackbar } from "@material-ui/core";
import { TransitionProps } from "@material-ui/core/transitions";
import { Alert } from "@material-ui/lab";
import React, { ReactElement, useState } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/src/interfaces/IProcessTemplate";
import { IRegionConfiguration } from "../../../../estreeh-common-library/src/interfaces/IRegionConfiguration";
import Branding from "../../config/Branding";
import ProcessTemplateCard from "./ProcessTemplateCard";

interface Props {
  process: IProcessTemplate;
  region: IRegionConfiguration;
  newItem: boolean;
}

function SlideTransition(props: TransitionProps) {
  return <Slide {...props} direction="down" />;
}

export default function ProcessTemplate(props: Props): ReactElement {
  const [editing, setEditing] = useState(props.newItem);
  const [openSuccessMessage, setOpenSuccessMessage] = useState(false);

  function snackOnClose(event?: React.SyntheticEvent, reason?: string) {
    // if (reason === "clickaway") {
    //   return;
    // }
    setOpenSuccessMessage(false);
  }

  return (
    <Box
      boxShadow={Branding.shadow.niceShadow}
      borderRadius={Branding.borderRadius.niceRadius}
      border={
        editing
          ? "1px solid red"
          : "1px solid " + Branding.lighter
      }
      p={1}
      my={1}
    >
      <Snackbar
        TransitionComponent={SlideTransition}
        open={openSuccessMessage}
        autoHideDuration={4000}
        onClose={snackOnClose}
        anchorOrigin={{
          horizontal: "right",
          vertical: "top",
        }}
      >
        <Alert>Process settings saved successfully!</Alert>
      </Snackbar>
      <Grid container spacing={2} alignItems="flex-end">
        <Grid item md={10} xs={12}>
          <ProcessTemplateCard process={props.process} region={props.region} editing={editing} />
        </Grid>
        <Grid item md={2} xs={12}>
          <Box p={1}>
            {editing && (
              <Box>
                <Box my={1} textAlign="center" fontSize=".85rem" color="red" fontStyle="italic">Please save your changes</Box>
                <Button
                  fullWidth={true}
                  color="primary"
                  variant="outlined"
                  onClick={() => {
                    setEditing(false);
                    setOpenSuccessMessage(true);
                  }}
                >
                  Save
                </Button>
              </Box>
            )}
            {!editing && (
              <Button
                fullWidth={true}
                variant="outlined"
                color="primary"
                onClick={() => {
                  setEditing(true);
                }}
              >
                Edit
              </Button>
            )}
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}
