import * as Factory from "factory.ts";
import Faker from "faker";
import { IProcessTemplate } from "../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import { nationalities } from "../config/Nationalities";

export const ProcessTemplateFactory =
  Factory.Sync.makeFactory<IProcessTemplate>({
    title: Factory.each(
      (i) =>
        Faker.random.arrayElement(nationalities) + "-" + Faker.name.lastName()
    ),
    accommodationFeesBhd: Factory.each(() =>
      Faker.datatype.number({ min: 30, max: 90 })
    ),
    insuranceFeesBhd: Factory.each(() =>
      Faker.datatype.number({ min: 30, max: 90 })
    ),
    otherFeesBhd: Factory.each(() =>
      Faker.datatype.number({ min: 30, max: 90 })
    ),
    otherFeesLabel: Factory.each(() => "Others"),
    processFeesBhd: Factory.each(() =>
      Faker.datatype.number({ min: 700, max: 1200 })
    ),
    processDurationMinimumMonths: Factory.each(() =>
      Faker.datatype.number({ min: 1.1, max: 2.2 })
    ),
    processDurationMaximumMonths: Factory.each(() =>
      Faker.datatype.number({ min: 2.3, max: 7.5 })
    ),
  });
