import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  TextField,
} from "@material-ui/core";
import { Link, RouteComponentProps, navigate } from "@reach/router";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";

interface Props extends RouteComponentProps {}

export default function LoginPage(props: Props): ReactElement {
  return (
    <Box>
      <Box
        p={3}
        maxWidth={440}
        marginLeft="auto"
        marginRight="auto"
        my={5}
        boxShadow={Branding.shadow.niceShadow}
        borderRadius={Branding.borderRadius.niceRadius}
      >
        <form>
          <TextField id="username" label="Username" variant="outlined" />
          <TextField id="username" label="Password" variant="outlined" />
          <Box py={0}>
            <Grid container spacing={1}>
              <Grid item xs={6}>
                <FormControlLabel
                  control={
                    <Checkbox name="checkedB" color="primary" size="small" />
                  }
                  label="Keep me Logged In"
                />
              </Grid>
              <Grid item xs={6}>
                <Box textAlign="right">
                  <Link to="reset-password">
                    <Box lineHeight="38px">Forgot password?</Box>
                  </Link>
                </Box>
              </Grid>
            </Grid>
          </Box>
          <Box py={3} textAlign="center">
            <Button
              variant="contained"
              color="primary"
              onClick={() => {
                navigate("/panel");
              }}
            >
              Login
            </Button>
          </Box>
        </form>
      </Box>
    </Box>
  );
}
