import { Box } from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import { RouteComponentProps } from "@reach/router";
import React from "react";
import PageTitle from "../common/PageTitle";
import SubmitButton from "../forms/SubmitButton";

interface Props extends RouteComponentProps {}

export const AgencyPaymentMethodsPage = (props: Props) => {
  return (
    /* ----------------------------------------
  TODO: IDEAS to be in this page:

  - On/Off Payment methods support:
  --- Cash
  --- VISA
  --- Benefit
  --- Debit Card

  -----------------------------------------*/
    <Box>
      <PageTitle title="Payment Methods" />
      <Box>
        <Alert severity="info">Currently only Cash option is available. More options will be available in near future.</Alert>
      </Box>
      <SubmitButton
        title="Exit"
        navigate={props.navigate}
        navigateUrl="/panel/"
      />
    </Box>
  );
};
