import * as Factory from "factory.ts";
import Faker from "faker";
import { demoWorkerNames } from "../config/DemoItems";
import {
  complexions,
  educationLevels,
  jobTitles,
  languageSkills,
  maritalStatus,
  maxAge,
  maxExperience,
  maxProcessingFees,
  maxSalary,
  maxWeight,
  minAge,
  minExperience,
  minProcessingFees,
  minSalary,
  minWeight,
  otherSkills,
  religions,
} from "../config/FilterOptions";
import IWorkerTwoYear from "../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import JobExperienceFactory from "./JobExperienceFactory";
import LanguageSkillFactory from "./LanguageSkillFactory";

const WorkerTwoYearFactory = Factory.Sync.makeFactory<IWorkerTwoYear>({
  id: Factory.each((i) => i + 1),
  age: Factory.each(() => Faker.random.number({ min: minAge, max: maxAge })),
  hidden: false,
  // bigPicture: Factory.each(
  //   () =>
  //     "http://placeimg.com/" +
  //     Faker.random.number({ min: 550, max: 555 }) +
  //     "/" +
  //     Faker.random.number({ min: 500, max: 505 }) +
  //     "/people"
  // ),
  // smallPicture: Factory.each(
  //   () =>
  //     "http://placeimg.com/" +
  //     Faker.random.number({ min: 115, max: 120 }) +
  //     "/" +
  //     Faker.random.number({ min: 100, max: 105 }) +
  //     "/people"
  // ),
  smallPicture: Factory.each(() => ""),
  bigPicture: Factory.each(() => ""),
  // name: Factory.each(() => Faker.name.findName()),
  name: Factory.each(() => Faker.random.arrayElement(demoWorkerNames)),
  jobTitle: Factory.each(() => Faker.random.arrayElement(jobTitles)),
  // nationality: Factory.each(() => ({
  //   title: Faker.random.arrayElement(nationalities),
  // })),
  nationality: Factory.each(() => ({
    title: Faker.random.arrayElement([
      "Philippines",
      "Philippines",
      "India",
    ]),
  })),
  monthlySalaryBhd: Factory.each(
    () => Faker.random.number({ min: minSalary / 25, max: maxSalary / 25 }) * 25
  ),
  experienceYears: Factory.each(() =>
    Faker.random.number({ min: minExperience, max: maxExperience })
  ),
  maritalStatus: Factory.each(() => Faker.random.arrayElement(maritalStatus)),
  educationalLevel: Factory.each(() =>
    Faker.random.arrayElement(educationLevels)
  ),
  demoLanguageSkillsText: Factory.each(() =>
    Faker.random.arrayElement(languageSkills)
  ),
  processFeesBhd: Factory.each(
    () =>
      Faker.random.number({
        min: minProcessingFees / 50,
        max: maxProcessingFees / 50,
      }) * 50
  ),
  remarks: Factory.each(() => Faker.lorem.paragraph()),
  numberOfChildren: Factory.each(() => Faker.random.number({ min: 0, max: 5 })),
  addressOriginalCountry: Factory.each(() => ({
    city: Faker.address.city(),
  })),
  cityOfBirth: Factory.each(() => Faker.address.city()),
  dateOfBirth: Factory.each(() =>
    Faker.date.between("1962-01-01", "2000-01-01")
  ),
  phone: Factory.each(() => Faker.phone.phoneNumber()),
  weightKg: Factory.each(() =>
    Faker.random.number({ min: minWeight, max: maxWeight })
  ),
  heightCm: Factory.each(() => Faker.random.number({ min: 120, max: 180 })),
  religion: Factory.each(() => Faker.random.arrayElement(religions)),
  jobExperiences: Factory.each(() =>
    JobExperienceFactory.buildList(Faker.random.number({ min: 1, max: 4 }))
  ),
  isLocalTransfer: Factory.each(() =>
    Faker.random.arrayElement([false, false, false, false, true])
  ),
  languageSkills: Factory.each(() => [
    LanguageSkillFactory.build({
      language: "English",
    }),
    LanguageSkillFactory.build({
      language: "Arabic",
    }),
  ]),
  otherSkills: Factory.each(() =>
    Faker.random.arrayElements(
      otherSkills,
      Faker.random.number({ min: 2, max: 4 })
    )
  ),
  referenceId: Factory.each(
    () =>
      Faker.random.alpha({ count: 3, upcase: true }) +
      Faker.random.number({ min: 1000, max: 9999 })
  ),
  complexion: Factory.each(() => Faker.random.arrayElement(complexions)),
  agency: null,
});

export default WorkerTwoYearFactory;
