import { Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns } from "@material-ui/data-grid";
import { RouteComponentProps } from "@reach/router";
import { ReactElement } from "react";
import IWorkerTwoYear from "../../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import { IApplicationForm } from "../../../../estreeh-common-library/src/interfaces/IApplicationForm";
import { ApplicationFormFactory } from "../../factories/ApplicationFormFactory";
import Faker from "faker";
import { Edit } from "@material-ui/icons";
import { WorkerWorkflowStatuses } from "../../config/WorkerWorkflowDropdown";
import PageTitle from "../common/PageTitle";

interface Props extends RouteComponentProps {}

export default function MyRequestsPage(props: Props): ReactElement {
  const columns: GridColumns = [
    {
      field: "id",
      headerName: "Request #",
      sortable: true,
      width: 140,
    },

    {
      field: "REF",
      sortable: false,
      width: 130,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.referenceId;
      },
    },
    {
      field: "Worker Name",
      sortable: true,
      width: 200,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.name;
      },
    },
    {
      field: "status",
      headerName: "Status",
      sortable: true,
      width: 180,
      valueGetter: (params) => {
        return Faker.random.arrayElement(WorkerWorkflowStatuses);
      },
    },
    {
      field: "fullName",
      headerName: "Applicant Name",
      sortable: true,
      width: 250,
    },

    {
      field: "mobileNumber",
      headerName: "Mobile",
      sortable: true,
      width: 200,
    },
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Actions",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                if (props.navigate) props.navigate("update");
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },

    // {
    //   field: "specialNotes",
    //   sortable: true,
    //   width: 200,
    // },
  ];

  const applications: IApplicationForm[] = ApplicationFormFactory.buildList(14);
  console.table(applications);

  return (
    <Box>
      <PageTitle title="My Requests" />
      <Box height={400}>
        <DataGrid
          disableSelectionOnClick={true}
          rows={applications}
          columns={columns}
          pageSize={5}
          // checkboxSelection
        />
      </Box>
    </Box>
  );
}
