import * as Factory from "factory.ts";
import Faker from "faker";
import { IApplicationForm } from "../../../estreeh-common-library/build/interfaces/IApplicationForm";
import dateFormat from "dateformat";
import WorkerTwoYearFactory from "./WorkerTwoYearFactory";

export const ApplicationFormFactory =
  Factory.Sync.makeFactory<IApplicationForm>({
    id: Factory.each((i) => i + 1),
    fullName: Factory.each(() => Faker.name.findName()),
    mobileNumber: Factory.each(
      () => "+973" + Faker.datatype.number({ min: 30000000, max: 79999999 })
    ),
    address: Factory.each(() => Faker.address.streetAddress()),
    selectedHours: Factory.each(() =>
      Faker.random.arrayElements(
        [
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
        ],
        Faker.datatype.number({ min: 2, max: 2 })
      )
    ),
    selectedMonths: Factory.each(() =>
      Faker.random.arrayElements(
        [Faker.date.month(), Faker.date.month(), Faker.date.month()],
        Faker.datatype.number({ min: 1, max: 2 })
      )
    ),
    paymentSummaryItems: [],
    paymentRemarks: "",
    selectedHoursFrequency: Faker.random.arrayElement([
      "One Time",
      "Every Week",
    ]),
    workerTwoYear: Factory.each(() => WorkerTwoYearFactory.build()),
  });
