import { Redirect, Router } from "@reach/router";
import { ReactElement } from "react";
import DashboardPage from "../dashboard/DashboardPage";
import AdminPanelLayout from "../layouts/AdminPanelLayout";
import EmptySection from "./EmptySection";
import LoginLayout from "../layouts/LoginLayout";
import NotFoundPage from "../layouts/NotFoundPage";
import LoginPage from "../login/LoginPage";
import AddWorkerPage from "../workers/AddWorkerPage";
import MyRequestsPage from "../requests/MyRequestsPage";
import MyWorkersPage from "../workers/MyWorkersPage";
import EditWorkerPage from "../workers/EditWorkerPage";
import UpdateRequestPage from "../requests/UpdateRequestPage";
import AgencyTermsPage from "../settings/AgencyTermsPage";
import SuccessWorkerPage from "../workers/SuccessWorkerPage";
import SuccessRequestPage from "../requests/SuccessRequestPage";
import { AgencyProfilePage } from "../settings/AgencyProfilePage";
import { NationalitiesPage } from "../settings/NationalitiesPage";
import { AgencyPaymentMethodsPage } from "../settings/AgencyPaymentMethodsPage";
import SuccessAgencyPage from "../settings/SuccessAgencyPage";
import AgencyProcessTemplatesPage from "../settings/AgencyProcessTemplatesPage";

interface Props {}

export default function MasterRouter(props: Props): ReactElement {
  return (
    <Router primary={false}>
      <NotFoundPage default />
      <Redirect from="/" to="/login" noThrow />
      <LoginLayout path="login">
        <LoginPage path="/" />
      </LoginLayout>
      <AdminPanelLayout path="panel">
        <Redirect from="/" to="dashboard" noThrow />
        <DashboardPage path="dashboard" />
        <EmptySection path="my-workers">
          <MyWorkersPage path="/" />
          <AddWorkerPage path="add" />
          <EditWorkerPage path="edit" />
          <SuccessWorkerPage path="success" />
        </EmptySection>
        <EmptySection path="my-requests">
          <MyRequestsPage path="/" />
          <UpdateRequestPage path="update" />
          <SuccessRequestPage path="success" />
        </EmptySection>
        <EmptySection path="agency/settings">
          <AgencyProfilePage path="profile" />
          <NationalitiesPage path="nationalities" />
          <AgencyTermsPage path="terms" />
          <AgencyPaymentMethodsPage path="payment-methods" />
          <SuccessAgencyPage path="success"/>
          <AgencyProcessTemplatesPage path="process-templates" />
        </EmptySection>
      </AdminPanelLayout>
    </Router>
  );
}
