import { Box, Button } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import { Regions } from "../../config/Regions";
import { ProcessTemplateFactory } from "../../factories/ProcessTemplateFactory";
import PageTitle from "../common/PageTitle";
import SubmitButton from "../forms/SubmitButton";
import ProcessTemplate from "./ProcessTemplate";

interface Props extends RouteComponentProps {}

export default function AgencyProcessTemplatesPage(props: Props): ReactElement {

  const makeBlankProcess = (): IProcessTemplate => {
    return ProcessTemplateFactory.build({
      accommodationFeesBhd: 0,
      insuranceFeesBhd: 0,
      otherFeesBhd: 0,
      processFeesBhd: 0,
      processDurationMaximumMonths: 0,
      processDurationMinimumMonths: 0,
      title: "",
    });
  };
  const fakeProcesses: IProcessTemplate[] = ProcessTemplateFactory.buildList(3);

  const [processes, setProcesses] = useState([...fakeProcesses, makeBlankProcess()]);

  const addClicked = () => {
    setProcesses((prev) => [
      ...prev,
      makeBlankProcess()
    ]);
  };

  return (
    <Box>
      <PageTitle title="Process Templates" />
      <Box>
        {processes.map((process, index) => (
          <ProcessTemplate process={process} region={Regions.bahrain} newItem={process.processFeesBhd === 0} />
        ))}
      </Box>
      <Box>
        <Button color="primary" variant="contained" onClick={addClicked}>
          + Add More
        </Button>
      </Box>
      <SubmitButton title="Exit" navigate={props.navigate} navigateUrl="/panel/dashboard" />
    </Box>
  );
}
