import { Box, Grid, TextField } from "@material-ui/core";
import React, { ReactElement, useState } from "react";
import { IOpeningHoursItem } from "../../../../estreeh-common-library/build/interfaces/IOpeningHoursItem";
import BackendField, { CamelToSentence } from "../forms/BackendField";

interface Props {
  openingHour: IOpeningHoursItem;
}

export default function OpeningHoursRow(props: Props): ReactElement {
  const oh = props.openingHour;
  const [open, setOpen] = useState(oh.open);
  return (
    <Box>
      <Grid container spacing={1} alignItems="center">
        <Grid item md={2}>
          {CamelToSentence(oh.day)}
        </Grid>
        <BackendField
          field={{
            name: open ? "Open" : "Closed",
            variant: "switch",
            value: open,
            onChange: () => {
              setOpen(!open);
            },
          }}
          size="small"
          defaultMd={2}
        />

        {open && (
          <>
            <BackendField
              field={{ name: "From", variant: "select" }}
              size="small"
              defaultMd={2}
            />
            <BackendField
              field={{ name: "To", variant: "select" }}
              size="small"
              defaultMd={2}
            />
            <BackendField
              field={{ name: "From", variant: "select" }}
              size="small"
              defaultMd={2}
            />
            <BackendField
              field={{ name: "To", variant: "select" }}
              size="small"
              defaultMd={2}
            />
          </>
        )}
        {!open && (
          <Grid item xs={4}>
            <Box
              style={{
                visibility: "hidden",
              }}
            >
              <TextField variant="outlined" size="small" />
            </Box>
          </Grid>
        )}
      </Grid>
    </Box>
  );
}
