import { Box } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";

import { IBackendField } from "../../../../estreeh-common-library/src/interfaces/IBackendField";
import { IOpeningHoursItem } from "../../../../estreeh-common-library/src/interfaces/IOpeningHoursItem";
import { OpeningHoursDay } from "../../../../estreeh-common-library/src/types/OpeningHoursDay";
import { AreasDropdown } from "../../config/AreasDropdown";
import PageTitle from "../common/PageTitle";
import BackendFieldSet from "../forms/BackendFieldSet";
import SubmitButton from "../forms/SubmitButton";
import OpeningHours from "./OpeningHours";
import Faker from "faker";
import { demoAgencies } from "../../config/DemoItems";
import { CamelToSentence } from "../forms/BackendField";

interface Props extends RouteComponentProps {}

export const AgencyProfilePage = (props: Props) => {
  const agencyName = Faker.random.arrayElement(demoAgencies);

  const fields: IBackendField[] = [
    {
      name: "name",
      label: "Agency Name (English)",
      readOnly: true,
      defaultValue: agencyName.name,
    },
    {
      name: "nameArabic",
      label: "Agency Name (Arabic)",
      readOnly: true,
      defaultValue: agencyName.nameAr,
    },
    {
      name: "CR",
      readOnly: true,
      defaultValue:
        Faker.datatype.number({ min: 10000, max: 99999 }) +
        "-" +
        Faker.datatype.number({ min: 0, max: 9 }),
    },
    {
      name: "area",
      label: "Agency Area",
      // variant: "select",
      readOnly: true,
      defaultValue: CamelToSentence(
        Faker.random.arrayElement(AreasDropdown.map((a) => a.value))
      ),
      // selectOptions: AreasDropdown,
    },
    {
      name: "officePhoneNo",
    },
    {
      name: "mobileNo",
    },
    {
      name: "email",
    },
  ];

  const days: OpeningHoursDay[] = [
    "sunday",
    "monday",
    "tuesday",
    "wednesday",
    "thursday",
    "friday",
    "saturday",
  ];

  const pictureFields: IBackendField[] = [
    {
      name: "logo",
      variant: "file",
    },
    {
      name: "banner",
      variant: "file",
      avatarWidth: 80,
    },
  ];

  // const unitField: IBackendField = {
  //   name: "unit",
  //   variant: "select",
  //   selectOptions: [{ value: "weeks" }, { value: "months" }],
  // };

  const openingHours: IOpeningHoursItem[] = days.map((day) => {
    const r: IOpeningHoursItem = {
      day: day,
      open: day === "friday" ? false : true,
      fromOne: "",
      toOne: "",
    };
    return r;
  });
  return (
    <Box>
      <PageTitle title="Profile" />
      <BackendFieldSet header="Agency Profile" fields={fields} md={6} />
      <BackendFieldSet header="Opening Days" md={7} xs={12}>
        <OpeningHours openingHours={openingHours} />
      </BackendFieldSet>
      <BackendFieldSet header="Images" md={3} fields={pictureFields} />
      <SubmitButton title="Save" navigate={props.navigate} />
    </Box>
  );
};
