import * as Factory from "factory.ts";
import Faker from "faker";
import IJobExperience from "../../../estreeh-common-library/build/interfaces/IJobExperience";

const JobExperienceFactory = Factory.Sync.makeFactory<IJobExperience>({
  country: Factory.each(() => Faker.address.country()),
  fromYear: Factory.each(() => Faker.random.number({ min: 1980, max: 1985 })),
  toYear: Factory.each(() => Faker.random.number({ min: 1985, max: 2005 })),
  totalYears: Factory.each(() => Faker.random.number({ min: 1, max: 6 })),
});

export default JobExperienceFactory;
