import { Box } from "@material-ui/core";
import React, { ReactElement, useState } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import { IRegionConfiguration } from "../../../../estreeh-common-library/build/interfaces/IRegionConfiguration";
import { IBackendField } from "../../../../estreeh-common-library/src/interfaces/IBackendField";
import BackendFieldSet from "../forms/BackendFieldSet";

interface Props {
  process: IProcessTemplate;
  region: IRegionConfiguration;
  editing: boolean;
}

export default function ProcessTemplateCard(props: Props): ReactElement {
  const p = props.process;
  const currencyCodeSuffix = props.region.currencyCode;
  const f = props.region.fixedProcessFees;

  const [processFees, setprocessFees] = useState(p.processFeesBhd);
  const [insuranceFees, setinsuranceFees] = useState(p.insuranceFeesBhd);
  const [accommodationFees, setaccommodationFees] = useState(
    p.accommodationFeesBhd
  );
  const [othersFees, setothersFees] = useState(p.otherFeesBhd);

  const formatted = (n:number):string => {
    // const mult = (Math.pow(10,props.region.currencyDecimals));
    // return Math.round(n * mult) / mult;
    return n.toFixed(props.region.currencyDecimals);
  }

  const sumFeesNoVat = (): number => {
    return (
      f.lmraFees +
      f.workPermitFees +
      processFees +
      insuranceFees +
      othersFees +
      accommodationFees
    );
  };

  const vatFees = (): number => {
    return (
      (processFees + insuranceFees + othersFees + accommodationFees) *
      f.vatPercentage
    );
  };

  const totalFees = (): number => {
    return sumFeesNoVat() + vatFees();
  };

  const fields: IBackendField[] = [
    {
      name: "title",
      defaultValue: p.title,
      readOnly: !props.editing,
    },
  ];

  const fieldsFixedFees: IBackendField[] = [
    {
      name: "LMRA Fees",
      readOnly: true,
      defaultValue: f.lmraFees,
      suffix: currencyCodeSuffix,
    },
    {
      name: "Permit Fees",
      readOnly: true,
      defaultValue: f.workPermitFees,
      suffix: currencyCodeSuffix,
    },
    {
      name: "VAT %",
      readOnly: true,
      defaultValue: f.vatPercentage * 100,
      suffix: "%",
    },
  ];

  const fieldsFees1: IBackendField[] = [
    {
      name: "processFees",
      readOnly: !props.editing,
      // defaultValue: p.processFeesBhd > 0 ? p.processFeesBhd : undefined,
      value: processFees,
      suffix: currencyCodeSuffix,
      type: "number",
      onChange: (e) => {
        setprocessFees(parseInt(e.target.value));
      },
    },
    {
      name: "insuranceFees",
      readOnly: !props.editing,
      // defaultValue: p.insuranceFeesBhd > 0 ? p.insuranceFeesBhd : undefined,
      value: insuranceFees,
      suffix: currencyCodeSuffix,
      type: "number",
      onChange: (e) => {
        setinsuranceFees(parseInt(e.target.value));
      },
    },
    {
      name: "accommodationFees",
      // defaultValue: p.accommodationFeesBhd > 0 ? p.accommodationFeesBhd : undefined,
      value: accommodationFees,
      readOnly: !props.editing,
      suffix: currencyCodeSuffix,
      type: "number",
      onChange: (e) => {
        setaccommodationFees(parseInt(e.target.value));
      },
    },
  ];

  const fieldsFeesOther: IBackendField[] = [
    {
      name: "othersName",
      defaultValue: p.otherFeesLabel,
      readOnly: !props.editing,
      variant: "select",
      // md: 6,
      selectOptions: [
        {
          value: "Others",
        },
        {
          value: "Travel Insurance & Training",
        },
      ],
    },
    {
      name: "othersFees",
      // defaultValue: p.otherFeesBhd > 0 ? p.otherFeesBhd : undefined,
      value: othersFees,
      readOnly: !props.editing,
      suffix: currencyCodeSuffix,
      type: "number",
      onChange: (e) => {
        setothersFees(parseInt(e.target.value));
      },
    },

  ];

  const fieldsTotals: IBackendField[] = [
    {
      name: "Sum",
      value: formatted(sumFeesNoVat()),
      readOnly: true,
      suffix: currencyCodeSuffix,
    },
    {
      name: "VAT",
      value: formatted(vatFees()),
      readOnly: true,
      suffix: currencyCodeSuffix,
    },
    {
      name: "totalFees",
      value: formatted(totalFees()),
      readOnly: true,
      suffix: currencyCodeSuffix,
    },
  ];

  const fieldsDurations: IBackendField[] = [
    {
      name: "minimumMonths",
      label: "Minimum",
      type: "number",
      readOnly: !props.editing,
      suffix: "Months",
      defaultValue:
        p.processDurationMinimumMonths > 0
          ? p.processDurationMinimumMonths
          : undefined,
    },
    {
      name: "maximumMonths",
      label: "Maximum",
      type: "number",
      readOnly: !props.editing,
      suffix: "Months",
      defaultValue:
        p.processDurationMaximumMonths > 0
          ? p.processDurationMaximumMonths
          : undefined,
    },
  ];

  return (
    <Box px={1}>
      <BackendFieldSet
        header="Process Name"
        fields={fields}
        md={6}
        headerVariant="small"
      />
      <BackendFieldSet
        header="Fees"
        fields={fieldsFixedFees}
        md={4}
        headerVariant="small"
      />
      <BackendFieldSet fields={fieldsFees1} md={4} headerVariant="small" />
      <BackendFieldSet fields={fieldsFeesOther} md={4} headerVariant="small" />
      <BackendFieldSet fields={fieldsTotals} md={4} headerVariant="small" />
      <BackendFieldSet
        header="Durations"
        fields={fieldsDurations}
        md={4}
        headerVariant="small"
      />
    </Box>
  );
}
