import {
  Box,
  Checkbox,
  FormControlLabel,
  FormGroup,
} from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import { nationalities } from "../../config/Nationalities";
import PageTitle from "../common/PageTitle";
import BackendFieldSet from "../forms/BackendFieldSet";
import SubmitButton from "../forms/SubmitButton";

interface Props extends RouteComponentProps {}

export const NationalitiesPage = (props: Props) => {

  return (
    <Box>
      <PageTitle title="Nationalities" />
      <BackendFieldSet xs={12}>
        <FormGroup row>
          {nationalities.map((nationality) => (
            <FormControlLabel
              control={
                <Checkbox name={"nationality_" + nationality} color="primary" />
              }
              label={nationality}
            />
          ))}
        </FormGroup>
      </BackendFieldSet>
      <SubmitButton title="Save" navigate={props.navigate} />
    </Box>
  );
};
