import React, { ReactElement } from "react";
import { RouteComponentProps } from "@reach/router";
import { Box, Container, Typography } from "@material-ui/core";
import Branding from "../../config/Branding";

interface Props extends RouteComponentProps {}

export default function NotFoundPage(props: Props): ReactElement {
  // navigate("/panel/myrequests");
  return (
    <Container>
      <Box m={5} p={3} boxShadow={Branding.shadow.niceShadow}>
        <Typography variant="h1">404</Typography>
        <Typography variant="h5">Page not found :(</Typography>

        <Box py={2}>
          <Typography>
            Sorry but the page you're looking for cannot be found. Please double
            check the link you have entered.
          </Typography>
        </Box>
      </Box>
    </Container>
  );
}
