import { Box } from "@material-ui/core";
import { Link } from "@reach/router";
import React, { ReactElement } from "react";
import { INavItem } from "../../../../estreeh-common-library/build/interfaces/INavItem";
import Branding from "../../config/Branding";

import Logo from "../common/Logo";
import LeftNavItem from "./LeftNavItem";

interface Props {}

export default function LeftNav(props: Props): ReactElement {
  const navs: INavItem[] = [
    {
      title: "Dashboard",
      to: "/panel/dashboard",
      icon: "arrow_forward_ios",
    },
    {
      title: "My Requests",
      to: "/panel/my-requests",
      icon: "arrow_forward_ios",
    },

    {
      title: "My Workers",
      to: "/panel/my-workers",
      icon: "arrow_forward_ios",
      children: [
        {
          title: "Manage Workers",
          to: "/panel/my-workers",
          // icon: "arrow_forward_ios",
        },
        {
          title: "Add New Worker",
          to: "/panel/my-workers/add",
          // icon: "arrow_forward_ios",
        },
      ],
    },

    {
      title: "Agency Settings",
      icon: "arrow_forward_ios",
      children: [
        {
          title: "Profile",
          to: "/panel/agency/settings/profile",
        },
        {
          title: "Nationalities",
          to: "/panel/agency/settings/nationalities",
        },
        {
          title: "Processes",
          to: "/panel/agency/settings/process-templates",
        },
        {
          title: "Payment Methods",
          to: "/panel/agency/settings/payment-methods",
        },

        {
          title: "Terms & Conditions",
          to: "/panel/agency/settings/terms",
        },
        // {
        //   title: "Workflow",
        // },
      ],
    },
    // {
    //   title: "Archive",
    //   to: "/panel/archive",
    //   icon: "arrow_forward_ios",
    // },
    {
      title: "Logout",
      to: "/login",
      icon: "logout",
    },
  ];

  return (
    <Box position="fixed" width="16.6666667%" height="100%">
      <Box bgcolor={Branding.primary} color="white" height="100%">
        <Box p={2} bgcolor={Branding.lighter} textAlign="left">
          <Link to="/panel" style={{ display: "inline-block" }}>
            <Logo />
          </Link>
        </Box>

        <Box px={0} py={4}>
          {navs.map((nav) => (
            <LeftNavItem my={2} mx={1} navItem={nav} key={nav.to} />
          ))}
        </Box>
      </Box>
    </Box>
  );
}
