import { Box } from "@material-ui/core";
import React, { ReactElement } from "react";
import { IOpeningHoursItem } from "../../../../estreeh-common-library/src/interfaces/IOpeningHoursItem";
import OpeningHoursRow from "./OpeningHoursRow";

interface Props {
  openingHours: IOpeningHoursItem[];
}

export default function OpeningHours(props: Props): ReactElement {
  return <Box>
    {props.openingHours.map((oh) => (
      <OpeningHoursRow openingHour={oh}/>
    ))}
  </Box>;
}
